import React, { useEffect } from 'react';
import '../App.css';

const ContentTexts = ({ texts, language, setAndUnfoldText }) => {
    
    return (
        <div className="flex flex-col flex-wrap content-center w-full">
            {texts.length > 0
                ?
                (texts.map((item) => (
                    <div className="slider relative -left-full group content-container w-11/12 md:w-10/12 lg:max-w-5xl md:max-w-6xl border-2 transform hover:-translate-y-1" onClick={(e) => { setAndUnfoldText(item, e) }} key={item.id}>
                        <h2 className="text-left font-bold border-b-2 border-gray-300 pb-6 text-xl lg:text-xl lg:pb-10">
                            {language === "EN" ? item.title_en : item.title_pl}
                        </h2>
                        <div className="QA-text mt-8 text-left overflow-auto transition-height duration-300 ease-in-out h-0">{language === "EN" ? item.text_en : item.text_pl}
                        </div>
                    </div>
                )))
                :
                (<div className="content-container max-w-6xl border-2 w-9/12" key="1">
                    <h2 className="text-left font-bold border-b-2 border-gray-300 pb-4">
                        {language === "EN" ? "There has been a problem" : "Wystąpił problem"}
                    </h2>
                    <div className="QA-text mt-5">
                        {language === "EN" ? "No article avilable right now" : "Nie znaleziono żadnego artykułu"}
                    </div>
                </div>)
            }
        </div>
    );
}

export default ContentTexts;
