import React from 'react';

const ContentHeader = ({ title, triggerDarkMode, changeLanguage, language, dark }) => {
    return (
        <header className="w-full">
            <button type="button" className="has-tooltip classic-btn dark:bg-gray-800 text-gray-500 dark:text-gray-200 hover:bg-green-200 dark:hover:bg-green-500 h-12 w-12 z-1 transition duration-300 ease-in-out absolute top-5 right-5 select-none" onClick={triggerDarkMode}>
                <span className="tooltip p-1 rounded border border-gray-200 bg-gray-100 dark:bg-gray-900 dark:text-gray-100 shadow-lg ml-4 text-sm w-28 absolute top-14 right-2">Change theme</span>

                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 animate-shortspin" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    {dark
                        ?
                        (<path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 3v1m0 16v1m9-9h-1M4 12H3m15.364 6.364l-.707-.707M6.343 6.343l-.707-.707m12.728 0l-.707.707M6.343 17.657l-.707.707M16 12a4 4 0 11-8 0 4 4 0 018 0z" />)
                        :
                        (<path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M20.354 15.354A9 9 0 018.646 3.646 9.003 9.003 0 0012 21a9.003 9.003 0 008.354-5.646z" />)
                    }
                </svg>
            </button>

            <button id="lang" type="button" className="has-tooltip classic-btn  dark:bg-gray-800 text-gray-500 dark:text-gray-200 hover:bg-green-200 dark:hover:bg-green-500 h-12 w-12 z-1 transition duration-300 ease-in-out absolute top-5 right-20 select-none" onClick={changeLanguage}>
                <span className="tooltip p-1 rounded border border-gray-200 bg-gray-100 dark:bg-gray-900 dark:text-gray-100 shadow-lg ml-4 text-sm w-32 absolute top-14 right-2">{language === "EN" ? "Change language" : "Zmień język"}</span>
                {language === "PL" ? "EN" : "PL"}
            </button>

            <div className="hero w-full h-64 flex flex-col mt-24 mb-12 font-bold shadow-lg">
                <h1 className="text-center text-2xl lg:text-3xl col-span-3 my-auto">
                    {title}
                </h1>
            </div>
        </header>
    );
}

export default ContentHeader;
